import React from 'react'
import './Recommendation.css'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';

import Juvenal from '../assets/Juvenal.JPG'
import Martial from '../assets/Martial.jpg'
import Michel from '../assets/Michel.jpg'
import Jacob from '../assets/Jacob.jpg'
import Jp from '../assets/Philipes.jpg'

function Recommendation() {

    const datas = [
        {
            id : 1,
            image: Juvenal,
            texte: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Facilis enim fuga temporibus laudantium eius eum nulla harum cupiditate tempore! Voluptates.',
            titre: 'Juvenal Pengele',
            emploi: 'Son emploi, entreprise'
        },

        {
            id : 2,
            image: Martial,
            texte: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Facilis enim fuga temporibus laudantium eius eum nulla harum cupiditate tempore! Voluptates.',
            titre: 'Martial Anouman',
            emploi: 'Son emploi, entreprise'
        },

        {
            id : 3,
            image: Michel,
            texte: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Facilis enim fuga temporibus laudantium eius eum nulla harum cupiditate tempore! Voluptates.',
            titre: 'Michel Dekado',
            emploi: 'Son emploi, entreprise'
        },

        {
            id : 4,
            image: Jacob,
            texte: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Facilis enim fuga temporibus laudantium eius eum nulla harum cupiditate tempore! Voluptates.',
            titre: 'Jacob Kouadio',
            emploi: 'Son emploi, entreprise'
        },

        {
            id : 5,
            image: Jp,
            texte: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Facilis enim fuga temporibus laudantium eius eum nulla harum cupiditate tempore! Voluptates.',
            titre: 'Jean Philipe Kablan',
            emploi: 'Son emploi, entreprise'
        }
    ]

    return (
        <>
            <div className="recommendation">

                <div className="container">
                    <div className="title">
                        <span>TEMOIGNAGES</span>
                        <h2>Recommandations</h2>
                    </div>

                    <div className="testimonial">
                        <Carousel autoPlay interval={5000} infiniteLoop showArrows={false} showStatus={false} >
                            {datas.map(slide => (
                                <div className="testimonial-items" key={slide.id} >
                                    <div className="testimonial-contents">

                                        <div className="testimonial-text">
                                            <p>{slide.texte}</p>
                                            <h3>{slide.titre}</h3>
                                            <p>{slide.emploi}</p>
                                        </div>

                                        <div className="testimonial-img">
                                            <img src={slide.image} alt="" />
                                        </div>
                                    </div>
                                </div>

                            ))}
                        </Carousel>

                    </div> 

                </div>

            </div>
            
        </>
    )
}

export default Recommendation

