import React from 'react'
import './Nav.css'
import {Link, NavLink} from 'react-router-dom'

function Nav() {
    return (
        <>

            <div className="nav">

                <ul className="nav-items">

                    <NavLink exact activeClassName="current" to="/">
                        <li>ACCUEIL</li>
                    </NavLink>

                    <NavLink exact activeClassName="current" to="/journey">
                        <li>MON PARCOURS</li>
                    </NavLink>

                    <NavLink exact activeClassName="current" to="/contact">
                        <li>CONTACT</li>
                    </NavLink>

                </ul>

            </div>
            
        </>
    )
}

export default Nav
